.product{
    width: 300px;
    height: 510px;
    border: 1px solid #95A0A7;
    border-radius: 8px;
    position: relative;
}
.product img{
    height: 286px;
    width: 286px;
    border-radius: 8px;
    margin: 7px;
    
}
.product-info{
    padding: 0 14px;
}
.product-info p{
    margin-bottom: 0;
    margin-top: 5px;
}
.product-name{
    font-family: "Lato";
    font-size: 21px;
    font-style: normal;
    font-weight: 400px;
    line-height:25px;
    margin-top: 7px;
}
.btn-cart{
    width:100%;
    background-color: #ffe0b3;
    border-radius: 0px 0px 8px 8px;
    border: none;
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.btn-cart:hover{
    background-color: orange;
}
.btn-text{
    margin-right: 10px;
}

